import React, { memo, useCallback, useMemo, useState } from "react";
import { connect } from 'react-redux';
import { createPortal } from "react-dom";
import { Tooltip } from 'react-tooltip';
import "./scss/Hotspot.scss"; 
import { generateUniqueString } from "../../../../utils/Common";
import { themePostTracking } from "../../../../actions/moredataAction";

// Memoized components to prevent unnecessary re-renders
const TooltipContent = memo(({ item }) => {
  const hasDiscount = item.discount > 0 && item.discount !== item.price;
  const displayPrice = item.discount > 0 ? item.discount : item.price;
  
  return (
    <a href={item.url} target="_blank" rel="noopener noreferrer" className="tb_tooltip_wrap">
      <div className="tb_p_tooltip_title">{item.title}</div>
      {hasDiscount && (
        <div className="tb_p_tooltip_price tb_price_disabled">
          {item.currency}{item.price}
        </div>
      )}
      {(item.price > 0 || item.discount > 0) && (
        <div className="tb_p_tooltip_price">
          {item.currency}{displayPrice}
        </div>
      )}
    </a>
  );
});

const HotspotComponent = ({ products, appData, Post }) => {
  const [activeIndex, setActiveIndex] = useState(null);
  
  // Memoized hotspot configuration
  const hotspotConfig = useMemo(() => (
    appData?.wall?.ProductSetting?.Hotspot || {}
  ), [appData]);

  // Stable event handlers
  const handleTracking = useCallback((product) => {
    if (appData?.wall) {
      themePostTracking({
      });
    }
  }, [appData, Post]);

  const handleHotspotClick = useCallback((event, item) => {
    event.stopPropagation();
    handleTracking(item);
    window.open(item.url, '_blank');
  }, [handleTracking]);

  if (!hotspotConfig?.status) return null;

  return products.map((item, index) => {
    const tooltipId = `hotspot-${item.id}-${generateUniqueString()}`;
    const isActive = activeIndex === index;
    
    return (
      <div key={`${item.id}-${index}`} id={tooltipId} className={`tb_hotspot_dot${isActive ? ' tb_hotspot_active_' : ''}`} role="button" style={{ 
          top: item.hotspot.top, 
          left: item.hotspot.left 
        }}
        onMouseEnter={() => setActiveIndex(index)} onMouseLeave={() => setActiveIndex(null)} onClick={(e) => handleHotspotClick(e, item)} data-tooltip-offset={7} >
        <div className={`tb_hotspot__ ${hotspotConfig.type === 0 ? 'tb_hotspot_ani__' : ''}`}>
          {index + 1}
        </div>
        
        {createPortal(
          <Tooltip className="tb_pro_tooltip" anchorSelect={`#${tooltipId}`} clickable >
            <TooltipContent item={item} />
          </Tooltip>,
          document.body
        )}
      </div>
    );
  });
};

// Memoize the main component
const MemoizedHotspot = memo(HotspotComponent);

// Optimized Redux connection
const mapState = (state) => ({ appData: state.appData });
export default connect(mapState)(MemoizedHotspot);