import React, { PureComponent, Suspense } from "react"; 
import { GET_IMAGE_SIZE } from '../../../../utils/Common'
import { CloudUrl } from "../../../../constants";
import ReactHtmlParser from "react-html-parser";
import { POPUP_IMAGE_RENEW_REQUEST } from "../../../../actions/ImageRenewAction";
import Hotspot from '../Hotspot/Hotspot';
export default class Image extends PureComponent {
    constructor(props) {
        super(props);
        const { mediaOnly, Post } = props;
        this.state = {
            isVisible: !this.props.observer, // Tracks if the image is in the viewport
            height: mediaOnly?.height || Post?.media?.height || 0,
            width:  mediaOnly?.width || Post?.media?.width || 0,
            currentHeight: 0,
            currentWidth: 0,
        };
        this.mediaRef = React.createRef();
        this.observer = null
    }
    

    componentDidMount() {
        this._isMounted = true;
        // Use requestAnimationFrame to ensure DOM is fully rendered
        requestAnimationFrame(() => {
            this.updateMediaDimensions();
            this.mediaSizeCalc();
        });

        // IntersectionObserver for visibility
        this.observer = new IntersectionObserver(
            ([entry]) => {
                if (entry.isIntersecting) {
                    this.setState({ isVisible: true});
                    this.observer.disconnect(); // Stop observing after the element becomes visible
                }
            },
            { threshold: 0.01 } // Adjust threshold as needed
        );

        if (this.mediaRef.current) {
            this.observer.observe(this.mediaRef.current);
        }

    }
    
        componentWillUnmount() {
            this._isMounted = false;
        if (this.observer) {
            this.observer.disconnect();
        }
    }


    updateMediaDimensions = () => {
        if (this.mediaRef.current) {
            const { offsetHeight, offsetWidth } = this.mediaRef.current;
            this.setState({
                currentHeight: offsetHeight,
                currentWidth: offsetWidth,
            });
        }
    };
    mediaSizeCalc = async () => {
        const { Post, mediaOnly } = this.props
        const media = mediaOnly ? mediaOnly : Post.media;
        if (!media?.height && !media?.width) {
            try {
                const { width, height } = await GET_IMAGE_SIZE(media?.image?.small);
                if (this._isMounted) {
                    this.setState({
                        height: height,
                        width: width,
                    });
                }
            } catch (error) {
                if (this._isMounted) {
                    this.setState({
                        height: 300,
                        width: 300,
                    });
                }
            }
        }
    }

    render() { 
        const { ImageClass, Post, hotspot, size, blurBG, ProductSetting, mediaOnly } = this.props;
        const { id, link, products } = Post
        const media = mediaOnly ? mediaOnly : Post?.media;
        const { isVisible, height, width, currentWidth, currentHeight } = this.state;

        const paddingSize = size || (height * 100) / width;

        const imageStyle = {
            paddingBottom: paddingSize === 1 ? null : `${paddingSize}%`};

        const blurImgStyle = {
            backgroundImage: isVisible ? `url(${media?.image?.small})` : null
        };

        const newWidth = width > height ? '100%' : `${(width / height) * 100}%`;
        const newHeight = width > height ? `${(height / width) * 100}%` : '100%';

        const isHotspot = Post.isProduct && hotspot;
        const hotspotImgStyle = { width: isHotspot && size ? newWidth : null, height: isHotspot && size ? newHeight : null };

        return (
            <div className={`${ImageClass}_wrap_`} style={imageStyle} ref={this.mediaRef}> 
                {isHotspot && blurBG ? <div className="tb_blur_bg_" style={blurImgStyle}></div> : null}
                <div className={`${ImageClass}_wrap_in`} style={hotspotImgStyle}>
                    {isHotspot ?
                    <Hotspot products={products} ProductSetting={ProductSetting} hotspot={Post.hotspot} dataPost={Post} />
                    : null}

                    { isVisible ?
                        <img
                            key={id}
                            className={`${ImageClass} tb_media-${id}`}
                            src={media?.image?.small}
                            srcSet={`${media?.image?.small} 1x, ${media?.image?.large} 2x`}
                            sizes={`${currentWidth}px`}
                            loading={isVisible ? "eager" : "lazy"}
                            decoding="async"
                            fetchPriority={isVisible ? "high" : "low"}
                            data-id={id}
                            height={currentHeight}
                            width={currentWidth}
                            data-height={height}
                            data-width={width}
                            data-link={link}
                            onLoad={this.onLoad}
                            onError={(e) => {
                                e.target.src = `${CloudUrl}/media/error/no-image.svg`
                                e.target.srcset = `${CloudUrl}/media/error/no-image.svg`
                                POPUP_IMAGE_RENEW_REQUEST(e)
                            }}
                            alt={media?.title}
                            title={ReactHtmlParser(media?.title)}
                        />
                        : null} 
                </div>
            </div>
        );
    }
}